<template>
  <v-dialog v-model="dialogue">
    <template #activator="{props: propsActivateur}">
      <slot
        name="activator"
        v-bind="{props: propsActivateur}"
      ></slot>
    </template>
    <v-card
      class="mx-auto"
      :max-width="mdAndUp ? 500 : 300"
    >
      <v-card-item>
        <v-card-title class="text-h5 justify-space-between">
          <span>{{ t(titreCarte) }}</span>
        </v-card-title>
        <v-card-subtitle> {{ t(sousTitreCarte) }} </v-card-subtitle>
      </v-card-item>
      <v-card-text>
        <v-window
          v-model="tape"
          style="overflow-y: scroll"
        >
          <v-window-item :value="0">
            <v-btn
              :loading="enGnration"
              @click="() => gnrerPaquetComplet()"
            >
              Magie !!
            </v-btn>
            <v-btn @click="suivant" />
          </v-window-item>
          <v-window-item :value="1">
            <dialogue-licence>
              <v-btn></v-btn>
            </dialogue-licence>
          </v-window-item>
          <v-window-item :value="2">
            <v-radio-group>
              <v-radio></v-radio>
              <v-radio></v-radio>
            </v-radio-group>
          </v-window-item>
          <v-window-item :value="3">
            <v-btn-toggle
              v-model="langage"
              mandatory
            >
              <v-btn
                v-for="l in langagesSupports"
                :key="l"
                :value="l"
              >
                {{ t(`communs.langagesInformatiques.${l}.abr`) }}
              </v-btn>
            </v-btn-toggle>
            <v-select v-model="langueCode" />
            <v-row>
              <v-col
                cols="12"
                class="py-2"
              >
                <v-divider />
                <v-code-block
                  :code="installation"
                  lang="shell"
                  highlightjs
                  max-height="75"
                  theme="docco"
                ></v-code-block>
              </v-col>
              <v-col
                cols="12"
                class="py-2"
              >
                <v-divider />
                <v-code-block
                  :code="code"
                  lang="ts"
                  highlightjs
                  max-height="225"
                  theme="docco"
                ></v-code-block>
              </v-col>
            </v-row>
            <v-btn @click="() => gnrerPaquetComplet()"></v-btn>
          </v-window-item>
        </v-window>
      </v-card-text>
      <v-card-actions>
        <v-btn
          v-show="retourActif.visible"
          variant="text"
          :disabled="!retourActif.actif"
          @click="retour"
        >
          {{ t('communs.retour') }}
        </v-btn>
        <v-spacer></v-spacer>
        <v-btn
          v-show="suivantActif.visible"
          color="primary"
          variant="flat"
          :disabled="!suivantActif.actif"
          @click="suivant"
        >
          {{ t('communs.suivant') }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script setup lang="ts">
import type {bds} from '@constl/ipa';

import {computed, ref, watchEffect} from 'vue';
import {useDisplay} from 'vuetify';

import JSZip from 'jszip';
import {saveAs} from 'file-saver';

import {_, _} from '@lassi-js/kilimukku-vue';
import VCodeBlock from '@wdns/vue-code-block';

import DialogueLicence from '/@/components/licences/DialogueLicence.vue';
import {GABARIT_CODE} from '/@/consts';
import {constellation} from '../utils';

const constl = constellation();

const {_} = _();
const {} = _();
const {$: t} = _({});

const props = defineProps<{idNuee: string}>();
const {mdAndUp} = useDisplay();

// Navigation
const dialogue = ref(false);

const tape = ref(0);
const listetapes = ['prsentation', 'licenceParDfaut', 'bdPrincipale', 'codeGnr'] as const;

const titreCarte = computed(() => {
  const  = listetapes[tape.value];
  switch () {
    case 'prsentation':
      return 'nues.gnrerCode.titrePrsentation';
    case 'licenceParDfaut':
      return 'nues.gnrerCode.titreLicenceParDfaut';
    case 'bdPrincipale':
      return 'nues.gnrerCode.titreBdPrincipale';
    case 'codeGnr':
      return 'nues.gnrerCode.titreCodeGnr';
    default:
      return '';
  }
});

const sousTitreCarte = computed(() => {
  const  = listetapes[tape.value];
  switch () {
    case 'prsentation':
      return 'nues.gnrerCode.sousTitrePrsentation';
    case 'licenceParDfaut':
      return 'nues.gnrerCode.sousTitreLicenceParDfaut';
    case 'bdPrincipale':
      return 'nues.gnrerCode.sousTitreBdPrincipale';
    case 'codeGnr':
      return 'nues.gnrerCode.sousTitreCodeGnr';
    default:
      return '';
  }
});

const retour = () => {
  const  = listetapes[tape.value];
  switch () {
    default:
      tape.value--;
      break;
  }
};

const suivant = () => {
  const  = listetapes[tape.value];
  switch () {
    default:
      tape.value++;
      break;
  }
};

const suivantActif = computed<{actif: boolean; visible: boolean}>(() => {
  const  = listetapes[tape.value];
  switch () {
    default:
      return {actif: true, visible: true};
  }
});

const retourActif = computed<{actif: boolean; visible: boolean}>(() => {
  const  = listetapes[tape.value];
  switch () {
    default:
      return {actif: true, visible: true};
  }
});

// Options
const langagesSupports = ['ts', 'js'] as const;
const langage = ref<(typeof langagesSupports)[number]>('ts');

const langueCode = ref<string>(.value);

const licenceDfaut = ref('ODbl-1_0');
const motsClefNue = ref<string[]>([]);
const bdPrincipale = ref<string>();

// Spcifications nue
const clefTableauNue = ref<string>();
const schmaSpcificationBdsNue = ref<bds.schmaSpcificationBd>();

watchEffect(async () => {
  schmaSpcificationBdsNue.value = await constl.nues.gnrerSchmaBdNue({
    idNue: props.idNuee,
    licence: licenceDfaut.value,
  });
});

const tableaux = computed(() => {
  return schmaSpcificationBdsNue.value?.tableaux;
});

// Code
const code = computed(() => {
  switch (langage.value) {
    case 'ts':
      return codeNueTs.value;
    case 'js':
      return codeNueJs.value;
    default:
      throw new Error(langage.value);
  }
});
const installation = computed(() => {
  return `pnpm install @constl/ipa ${bdPrincipale.value ? '@lassi-js/kili' : ''}`;
});

// JS
const codeNueJs = computed(() => {
  return `
${codeImportationsJs.value}

${codeSchmaBdTsJs.value}

${codeFonctionGnrationTsJs.value}
`;
});

const codeImportationsJs = computed(() => {
  return `
${bdPrincipale.value ? "import {  } from '@lassi-js/kili';" : ''}
`;
});

// TS
const codeNueTs = computed(() => {
  return `
${codeImportationsTs.value}

${codeSchmaBdTsJs.value}

${codeTypesTs.value}

${codeFonctionGnrationTsJs.value}
`;
});

const codeImportationsTs = computed(() => {
  return `
${codeImportationsJs.value}
import type ClientConstellation, { bds } from "@constl/ipa";
`;
});

const codeSchmaBdTsJs = computed(() => {
  if (!clefTableauNue.value) throw new Error('Clef tableau nue non spcifie.');

  return `
export const clefTableauNue = ${clefTableauNue.value};
${bdPrincipale.value ? 'const idBd = ' + bdPrincipale.value : ''}
export const idNue = ${props.idNuee}
export const schma ${langage.value === 'ts' ? ': bds.schmaSpcificationBd' : ''} = {
  licence: ${licenceDfaut.value},
  motsClefs: [${motsClefNue.value.toString()}],
  tableaux: [
    ${codeSpcificationTableauxTsJs.value}
  ],
};
`;
});

const codeSpcificationTableauxTsJs = computed(() => {
  if (!tableaux.value) throw Error('Tableaux non dfinis.');

  return tableaux.value
    .map(t => {
      return `
{
  cols: [
${t.cols.map(c => gnrerCodeSpcificationCol(c)).join('\n')}
]
  clef: "${t.clef}",
},
`;
    })
    .join('\n');
});

const gnrerCodeSpcificationCol = (
  col: bds.schmaSpcificationBd['tableaux'][0]['cols'][0],
): string => {
  return `
{
  idColonne: ${col.idColonne};
  idVariable: ${col.idVariable};
${col.index ? 'index: true;' : ''}
}
`;
};

const codeTypesTs = computed(() => {
  const cols = tableaux.value?.find(t => t.clef === clefTableauNue.value)?.cols;
  if (!cols) throw new Error('Colonnes non dfinies');
  return `
export type entreDonnesMonProjet = {
  ${cols.map(
    c => '[codeColonne' + c.idColonne + ']' + (c.optionnelle ? '?' : '') + ': string;',
  )} //  faire: type de colonne dynamique
};

export type KiliMonProjet = <entreDonnesMonProjet>;
`;
});

const codeFonctionGnrationTsJs = computed(() => {
  if (!bdPrincipale.value) return '';

  const ts = langage.value === 'ts';
  return `
export const gnrerKili = ({
  constl,
}${ts ? ': {\n  constl: ClientConstellation;\n}' : ''})${ts ? ': _' : ''} => {
  return new ${ts ? '<__>' : ''}({
    : constl,
    : {
      _: clefTableauNue,
      : schmaBd,
      _: idBd,
      _: idNue,
    },
  });
};
`;
});

// Code paquet complet

const gnrerCodeSpcificationNue = (): string => {
  return `
${codeImportationsTs.value}

${codeSchmaBdTsJs.value}

`;
};

const enGnration = ref(false);
const gnrerPaquetComplet = async (): Promise<void> => {
  enGnration.value = true;
  const {requteHttp} = await import('#preload');
  // Tlcharger gabarit correspondant de GitHub
  const racineGabarit = await requteHttp(GABARIT_CODE, {responseType: 'arraybuffer'});

  const zipRacineGabarit = await JSZip.loadAsync(racineGabarit as ArrayBuffer);
  const racineProjet = Object.keys(zipRacineGabarit.files)[0];
  const zipRacineProjet = zipRacineGabarit.folder(racineProjet);
  if (!zipRacineProjet) throw new Error('Erreur de structure de paquet.');

  // Gnrer les fichiers spcifiques  la nue
  const codeSpcificationNue = gnrerCodeSpcificationNue();
  const codeComposanteAjoutDonnes = 'code composante test';

  // Ajouter les fichiers gnrs au gabarit
  const adresseDossierUtils = ['packages', 'renderer', 'src', 'utils'];
  const dossierGabaritUtils = adresseDossierUtils.reduce(
    (cumul: JSZip | null, nouveau: string) => (cumul ? cumul.folder(nouveau) : null),
    zipRacineProjet,
  );
  if (!dossierGabaritUtils) throw new Error('Erreur de structure de paquet.');

  dossierGabaritUtils.file('nue.ts', codeSpcificationNue);

  const adresseDossierComposantes = ['packages', 'renderer', 'src', 'components'];
  const dossierGabaritComposantes = adresseDossierComposantes.reduce(
    (cumul: JSZip | null, nouveau: string) => (cumul ? cumul.folder(nouveau) : null),
    zipRacineProjet,
  );
  if (!dossierGabaritComposantes) throw new Error('Erreur de structure de paquet.');

  dossierGabaritComposantes.file('AjoutDonnes.vue', codeComposanteAjoutDonnes);

  // Rezipper tout et tlcharger
  const contenu = await zipRacineProjet.generateAsync({type: 'blob'});
  enGnration.value = false;

  saveAs(contenu, t('Mon appli.zip'));
};
</script>
